import {Component, OnInit} from '@angular/core';
import {ControllerService} from '../services/controller.service';
import {ConstantsService, PRIVILEGES} from '../services/constants.service';
import {Router} from '@angular/router';
import {AlertService} from '../services/alert.service';
import {GraphUI} from '@factman/graph-ui/js/GraphUI.min';

@Component({
  selector: 'app-admin-home',
  templateUrl: './admin-home.page.html',
  styleUrls: ['./admin-home.page.scss'],
})
export class AdminHomePage implements OnInit {

  orders: any[] = Object.values(this.api.getOrders());
  form = {
    orderId: '',
  };
  yearData = {
    'Jan': 0,
    'Feb': 0,
    'Mar': 0,
    'Apr': 0,
    'May': 0,
    'Jun': 0,
    'Jul': 0,
    'Aug': 0,
    'Sep': 0,
    'Oct': 0,
    'Nov': 0,
    'Dec': 0,
  };
  monthData = {
    'Mon': 0,
    'Tue': 0,
    'Wed': 0,
    'Thu': 0,
    'Fri': 0,
    'Sat': 0,
    'Sun': 0,
  };

  constructor(private api: ControllerService, private router: Router) {
  }

  ngOnInit() {
    const user = this.api.getCurrentUser();

    if (user !== PRIVILEGES.ADMIN) {
      this.router.navigateByUrl('/home');
    }

    this.orders.forEach((order: any) => {
      const date = new Date(order.createdAt);
      const year = date.getFullYear();
      const month = date.toDateString().split(' ')[1];
      const weekDay = date.toDateString().split(' ')[0];

      if (new Date().getFullYear() === year) {
        this.yearData[month] += order.total;
        if (new Date().getMonth() === date.getMonth()) {
          this.monthData[weekDay] += order.total;
        }
      }
    });

    const graph1 = new GraphUI({
      elementId: 'graph1',
      data: this.yearData,
      precision: 2,
      prefix: '₦',
      showTable: false,
      dropdownOptions: ['IMAGE | Save As Image'],
    });

    const graph2 = new GraphUI({
      elementId: 'graph2',
      data: this.monthData,
      precision: 2,
      prefix: '₦',
      showTable: false,
      dropdownOptions: ['IMAGE | Save As Image'],
    });

    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
    }, 2000);
  }

  logout() {
    this.api.logout(PRIVILEGES.ADMIN);
    location.assign('/home');
  }
}
